import { useState, useEffect } from 'react';
import { Link as RouterLink } from "react-router-dom";
import { AppBar, Toolbar, Typography, Button, Menu, MenuItem, Drawer, Link } from '@material-ui/core';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import MenuIcon from '@material-ui/icons/Menu';
import { IconButton  } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import logo from './images/logo.png';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    appbar: {
        color: '#000000',
        backgroundColor: '#ffffff',
        "@media (max-width: 900px)": {
            paddingLeft: 0,
          },
    },
    logo: {
        height: 100,
    },
    title: {
        flexGrow: 1,
    },
    button: {
        marginLeft: 24,
        marginRight: 24,
        '&:hover': {
            backgroundColor: '#ffffff',
            color: theme.palette.primary.dark,
        }
    },
    dropdownMenu: {
        minWidth: 200,
    }
}));

const headersData = [
    {
      label: "Strona główna",
      href: "/",
    },
    {
        href: "/kobido",
        label: "Kobido",
    },
    {
        href: "/kassaji",
        label: "Kassaji",
    },
    {
        href: "/kyukaku",
        label: "Kyukaku",
    },
    {
        href: "/dermoplastyczny",
        label: "Dermoplastyczny",
    },
    {
        href: "/mioplastyczny",
        label: "Mioplastyczny",
    },
    {
        href: "/transbukkalny",
        label: "Transbukkalny",
    },
    {
        href: "/zoga",
        label: "Zoga Face Integration",
    },
    {
        href: "/relaksacyjny",
        label: "Masaż relaksacyjny",
    },
    {
        href: "/kamienie",
        label: "Gorące kamienie",
    },
    {
        href: "/limfatyczny",
        label: "Drenaż limfatyczny",
    },
    {
        label: "Cennik",
        href: "/cennik",
    },
    {
        label: "Kontakt",
        href: "/kontakt",
    },

];
export default function AppNav() {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };

    const [state, setState] = useState({
        mobileView: false,
        drawerOpen: false
      });
    
      const { mobileView, drawerOpen } = state;
    
      useEffect(() => {
        const setResponsiveness = () => {
          return window.innerWidth < 900
            ? setState((prevState) => ({ ...prevState, mobileView: true }))
            : setState((prevState) => ({ ...prevState, mobileView: false }));
        };
    
        setResponsiveness();
        window.addEventListener("resize", () => setResponsiveness());
    
        return () => {
          window.removeEventListener("resize", () => setResponsiveness());
        }
      }, []);
    const handleDrawerOpen = () => setState((prevState) => ({ ...prevState, drawerOpen: true }));
    const handleDrawerClose = () => setState((prevState) => ({ ...prevState, drawerOpen: false }));
    
    const displayMobile = () => {
        return (
            <Toolbar>
                <Drawer
                    {...{
                        anchor: "right",
                        open: drawerOpen,
                        onClose: handleDrawerClose,
                    }}
                    >
                    <div>{getDrawerChoices()}</div>
                </Drawer>
                <Typography variant="h6" className={classes.title}>
                    <img alt="Vyspa" src={logo} className={classes.logo} />
                </Typography>
                <IconButton
                    {...{
                        edge: "start",
                        color: "inherit",
                        "aria-label": "menu",
                        "aria-haspopup": "true",
                        onClick: handleDrawerOpen,
                        }}
                >
                    <MenuIcon />
                </IconButton>
            </Toolbar>
        )
    }

    const getDrawerChoices = () => {
        return headersData.map(({ label, href }) => {
          return (
            <Link
              {...{
                component: RouterLink,
                to: href,
                color: "inherit",
                style: { textDecoration: "none" },
                key: label,
              }}
            >
              <MenuItem onClick={handleDrawerClose} >{label}</MenuItem>
            </Link>
          );
        });
      };    

    const displayDesktop = () => (
        <Toolbar>
            <Typography variant="h6" className={classes.title}>
                <img alt="Vyspa" src={logo} className={classes.logo} />
            </Typography>
            <Button color="inherit" component={RouterLink} to="/" className={classes.button} disableRipple disableFocusRipple>Strona główna</Button>
            <Button
                id="basic-button"
                aria-controls="basic-menu"
                aria-haspopup="true"
                aria-expanded={open ? 'true' : undefined}
                onClick={handleClick}
                endIcon={<ArrowDropDownIcon  />}
            >
                Usługi
            </Button>
            <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{
                    'aria-labelledby': 'basic-button',
                }}
            >
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/kobido">Kobido</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/kassaji">Kassaji</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/kyukaku">Kyukaku</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/dermoplastyczny">Dermoplastyczny</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/mioplastyczny">Mioplastyczny</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/transbukkalny">Transbukkalny</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/zoga">Zoga Face Integration</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/relaksacyjny">Masaż relaksacyjny</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/kamienie">Gorące Kamienie</MenuItem>
                <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={RouterLink} to="/limfatyczny">Drenaż limfatyczny</MenuItem>
            </Menu>
            <Button color="inherit" component={RouterLink} to="/cennik" className={classes.button} disableRipple disableFocusRipple>Cennik</Button>
            <Button color="inherit" component={RouterLink} to="/kontakt" className={classes.button} disableRipple disableFocusRipple>Kontakt</Button>
        </Toolbar>
    )
    
      
    return (
        <div className={classes.root}>
            <AppBar position="fixed" className={classes.appbar}>
                {mobileView ? displayMobile() : displayDesktop()}
            </AppBar>
        </div>
    )
}
