import { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Grid, Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper} from '@material-ui/core/';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    header: {
        marginTop: 100,
        minHeight: 150,
        backgroundColor: theme.palette.primary.main,
        textAlign: 'center',
        paddingTop: 40,
    },
    table: {
        paddingTop: 50,
    },
    tableHeader: {
        fontWeight: 900,
    }
}));


function createData(name, time, price) {
    return { name, time, price };
}

const rows = [
    createData('Kobido', '60 min', 180),
    createData('Kassaji', '40-60 min', 180),
    createData('Kyukaku', '60 min', 170),
    createData('Zoga Face Integration', '75-90 min', 200),
    createData('Transbukkalny', '60-75 min', 230),
    createData('Mioplastyczny', '60-75 min', 230),
    createData('Dermoplastyczny', '60-75 min', 230),
    createData('Psychosomatyczny Twarzy', '60 min', 230),
    createData('Drenaż Limfatyczny Twarzy', '60-75 min', 220),
    createData('Masaż relaksacyjny częściowy', '30 min', 60),
    createData('Masaż relaksacyjny całościowy', '60 min', 100),
    createData('Masaż gorącymi kamieniami częściowy', '45 min', 100),
    createData('Masaż gorącymi kamieniami całościowy', '80 min', 140),
];

export default function Cennik() {
    const classes = useStyles();

    useEffect(() => {
        window.scrollTo(0, 0)
      }, [])

    return (
        <div className={classes.root}>
            <Grid container direction="column">
                <Grid item className={classes.header}>
                    <Typography variant="h2">
                        Cennik
                    </Typography>
                </Grid>
                <Grid item className={classes.table}>
                    <Container maxWidth="md">
                        <TableContainer component={Paper}>
                            <Table className={classes.table} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell className={classes.tableHeader}>Nazwa zabiegu</TableCell>
                                        <TableCell className={classes.tableHeader} align="right">Czas trwania</TableCell>
                                        <TableCell className={classes.tableHeader} align="right">Cena</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {rows.map((row) => (
                                        <TableRow key={row.name}>
                                            <TableCell component="th" scope="row">
                                                {row.name}
                                            </TableCell>
                                            <TableCell align="right">{row.time}</TableCell>
                                            <TableCell align="right">{row.price} zł</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>
                </Grid>
            </Grid>
        </div>
    )
}
